.App {
  text-align: center;
}

.flex-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
}

.left-col {
  flex: 1;
  background-color: #c0B283;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.right-col {
  flex: 4;
  display: flex;
  flex-direction: column;
  background-color: #f4f4f4;
  background-image: url("/img/intro.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.self-pic {
  margin-top: 1vh;
  max-width: 95%;
  border-radius: 50%;
  border-color: #373737;
  padding: 3px;
  border-width: 3px;
  border-style: solid;
}

p {
  color: #373737;
  font-family: 'Raleway', sans-serif;
}

a {
  color: #373737;
  font-family: 'Raleway', sans-serif;
}

.text-padding {
  margin: 1vw;
}

.big-text-padding {
  margin: 8vw;
}

a:link {
  color:blue;
}

a:visited {
  color: blue;
}

.name {
  font-size: 3vh;
}

